.item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 1px solid black;
  padding: 5px;
  transform: scale(1);
  transition: transform 250ms ease;
}

.item:focus,
.item:hover {
  transform: scale(1.05);
}

.item:not(:last-child) {
  margin-bottom: 10px;
}
